* {
    box-sizing: border-box;
  }
  
  .auth_logo{
    margin: auto auto;
    width: 150px !important;
    height: auto;
}
  .form_main{
      width: 300px;
      padding: 10px;
      border: 1px solid rgb(195, 195, 195);
      border-radius: 5px;
      margin: 10px auto;
      box-shadow: 4px 14px 8px rgba(34, 35, 58, 0.2);
  }

  .btn{
      width: 150px;
      margin: 10px auto;
      padding: 5px;
  }

  .link{
      text-decoration: none;
  }
  .navbar-light {
    background-color: #ffffff;
   
  }
  
  .outer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
  }
  
  .inner {
    width: 450px;
    margin: auto;
    background: #ffffff;
    padding: 40px 55px 45px 55px;
    border-radius: 15px;
    transition: all .3s;
  }
  
  .outer .form-control:focus {
    border-color: #167bff;
    box-shadow: none;
  }
  
  .outer h3 {
    text-align: center;
    margin: 0;
    line-height: 1;
    padding-bottom: 20px;
  }
  
  .custom-control-label {
    font-weight: 400;
  }
  
  .forgot-password,
  .forgot-password a {
    text-align: right;
    font-size: 13px;
    padding-top: 10px;
    color: #7f7d7d;
    margin: 0;
  }
  
  .forgot-password a {
    color: #167bff;
  }